import { Routes } from '@angular/router';

import { AdminLayoutComponent } from './layouts/admin-layout/admin-layout.component';
import { AnswerComponent } from './pages/answers/answer.component';
import { ChatComponent } from './pages/chat/chat.component';
import { LoginComponent } from './pages/login/login.component';
import { CanActivateTokenService } from './Services/Shared/can-activate-token.service';
import { CanActivateService } from './Services/Shared/can-activate.service';

export const AppRoutes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
    canActivate:[CanActivateTokenService]
  },
  {
    path: 'chat',
    component: ChatComponent,
    canActivate:[CanActivateService]
  },
  {
    path: 'login',
    component:LoginComponent
  }
  , {
    path: '',
    component: AdminLayoutComponent,
    children: [
        {
      path: '',
      loadChildren: './layouts/admin-layout/admin-layout.module#AdminLayoutModule'
  }]},
    {
    path: '**',
    redirectTo: 'dashboard'
  }
]
