import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ServiceGeneral } from 'app/interfaces/services';
import { User } from 'app/models/user';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { SharedService } from '../Shared/shared.service';


@Injectable({
  providedIn: 'root'
})
export class AgentService extends ServiceGeneral<User> {
  public sendUrl;
  public startUrl;
  public endUrl;
  public api;
  public httpOptions:object = {}; 
  updateUrl: string;
  constructor(protected http:HttpClient,private shared:SharedService) {
    super(http);
    this.api = shared.api;
    this.sendUrl = `${this.api}/api/push`;
    this.startUrl = `${this.api}/api/start`;
    this.endUrl = `${this.api}/api/end`;
    this.updateUrl = `${this.api}/api/agents/update`;
    this.httpOptions = null;

    this.url = `${this.api}/api/agents`;
   };

   getUpdate(adapter): Observable<any>
   {
     this.httpOptions = {
      headers: new HttpHeaders({
        'Authorization' : `Bearer ${localStorage.getItem('token')}`,
        'Content-Type': 'application/json'
      })
    };
    return this.http.get(this.updateUrl,this.httpOptions).pipe(map((data: any)=> {
      return data.data.map((item) =>
      {
        return adapter.adapt(item)
      });
    }));
   }

   sendMessage(data) : Observable<any>
   {
    this.httpOptions = {
      headers: new HttpHeaders({
        'Authorization' : `Bearer ${localStorage.getItem('token')}`,
        'Content-Type': 'application/json'
      })
    };
      return this.http.post(this.sendUrl,data,this.httpOptions);
   }

   start(data) : Observable<any>
   {   
    this.httpOptions = {
      headers: new HttpHeaders({
        'Authorization' : `Bearer ${localStorage.getItem('token')}`,
        'Content-Type': 'application/json'
      })
    };  
     return this.http.post(this.startUrl,data,this.httpOptions);
   }

   end(data) : Observable<any>
   {
    this.httpOptions = {
      headers: new HttpHeaders({
        'Authorization' : `Bearer ${localStorage.getItem('token')}`,
        'Content-Type': 'application/json'
      })
    };
     return this.http.post(this.endUrl,data,this.httpOptions);
   }
}
