import { HttpClient, HttpHeaders } from '@angular/common/http';
import { resolve } from '@angular/compiler-cli/src/ngtsc/file_system';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';



@Injectable({providedIn: 'root'})

export class SharedService {
  public host = 'http://shjpolice.mworld.ae/Sharjah-Police/public';
  public hostForChat = 'http://shjpolice.mworld.ae';
  public api = this.host;
  // ********************************************
  // public host = 'https://stg-chatbot.shj.ae';
  // public hostForChat = 'https://stg-chatbotapp.shj.ae:3001';
  // public api = this.host + '/AwqafBot/public';
  // ********************************************
  public profile = `${this.api}/api/auth/profile`;
  public httpOptions;
  public roles = { 0 : ['answers','rating','suggestions','service','buisness','answer-detail'] , 1 : ['chat'] 
  , 2 : ['answer-report','agent-report'], 3 : ['agent_report','chat'], 
  4 : ['answers', 'users', 'home', 'rating','suggestions','service','buisness','answer-detail','chat','answer-report','agent-report']};

  constructor(private http:HttpClient) {
   }

   public getToken()
   {
     return localStorage.getItem('token');
   }

   public getProfile()
   {
    this.httpOptions = {headers: new HttpHeaders({
      'Authorization' : `Bearer ${localStorage.getItem('token')}`,
      'Content-Type': 'application/json'
    })};
    return this.http.get(this.profile,this.httpOptions);
   }



   public async checkIfUserCanAuthenticate(title:string)
   {
      let data = await this.getAgent();
      let roles = this.roles[data];
      console.log(roles, title);
      
      if(roles.indexOf(title) > -1)
      {
        return true;
      }
      else
      {
        return false;
      }
   }

   public getProfilePromise()
   {
        return new Promise(resolve =>
          {
            this.getProfile().subscribe((data) =>
            {
              resolve(data);
            })
          });      
   }

   async getAgent()
   {
      let data =  await this.getProfilePromise();
      return data['isAgent'];
   }

   async getId()
   {
   let data =  await this.getProfilePromise();
    return data['id'];
   }
}
