h: 100%;\n    margin: 0px 10px;\n    border: none;\n}\n\n.input-message:focus {\n    outline: none;\n}\n\n/* ------ SCROLLBAR ------ */\n\nbody::-webkit-scrollbar,\n.contact-list::-webkit-scrollbar,\n.chat::-webkit-scrollbar,\n.information::-webkit-scrollbar {\n    width: 0.4em;\n    height: 0.4em;\n}\n\nbody::-webkit-scrollbar-thumb,\n.contact-list::-webkit-scrollbar-thumb,\n.chat::-webkit-scrollbar-thumb,\n.information::-webkit-scrollbar-thumb {\n    background-color: rgba(0, 0, 0, 0.2);\n}\n\n/* ------ MEDIA QUERIES ------ */\n\n@media (min-width: 1200px) {\n    .wrap {\n        margin-bottom: 2vh;\n        margin-top: 25px;\n        height: calc(98vh - 25px);\n    }\n}\n\n@media (max-width: 600px) {\n    .wrap {\n        height: calc(100vh - 0.4em);\n    }\n}\n\n.contact .clk {\n    background-color: #085373;\n    border: 1px solid #085373;\n    color: #fff;\n}\n\n.contact .clk:focus {\n    outline: none;\n}\n\n.contact .clkStart {\n    background-color: #398b3f;\n    border: 1px solid #398b3f;\n    color: #fff;\n    text-align: center;\n}\n\n.contact .clkStart:focus {\n    outline: none;\n}\n\n.chat-right {\n    background: #ffffff;\n    margin: 0px auto 10px 0px;\n    font-family: 'DroidNaskh';\n}\n\n.chat-left {\n    background: #dcf8c6;\n    margin: 0px 0px 10px auto;\n    font-family: 'DroidNaskh';\n}\n\n.chat-left .your-mouth {\n    width: 0;\n    height: 0;\n    border-bottom: 10px solid #dcf8c6;\n    border-right: 10px solid transparent;\n    position: absolute;\n    bottom: 10px;\n    left: 100%;\n    border-left: none;\n}\n\n.chat .content {\n    font-family: 'DroidNaskh';\n    font-size: 14px;\n}\n/*# sourceMappingURL=data:application/json;base64, */";