import { Component, HostListener, OnInit,AfterViewChecked } from '@angular/core';
import { Router } from '@angular/router';
import { Adapter } from 'app/adapter';
import { Chat, ChatAdapter } from 'app/models/Chat';
import { Log, LogAdapter } from 'app/models/logs';
import { User, UserAdapter } from 'app/models/user';
import { AgentService } from 'app/Services/agent/agent.service';
import { LogService } from 'app/Services/logs/log.service';
import { SharedService } from 'app/Services/Shared/shared.service';
import { Observable } from 'rxjs';
const io = require('socket.io-client');
@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit,AfterViewChecked {
  // public host = "localhost"

  public data:User[] = [];
  public update:User[] = [];
  public logs:Log[];
  public userData:User = new User(0,'',0,false);
  public message:string = '';
  public body:any;
  public socket:any;
  public agent:any;
  public agentType:any;
  public api:any;
  public host:any;


  public constructor(private agentService:AgentService,private adapter:UserAdapter,private logService:LogService,private adapter2:LogAdapter,private router:Router,private shared:SharedService) { 
    this.api = shared.api;
    this.host = shared.host;
    this.socket = io(`${this.shared.hostForChat}:3000`, { withCredentials : true});
    this.agentService.index = 1;
    this.shared.getId().then((data) => 
    {
      this.agentType = data;
      this.agent = data;
      
    })

    this.agentService.getData(this.adapter).subscribe((data:User[])=>
    {
      console.log(data);
      
      if (data) {
        hideloader();
      }
      this.data = data;
      console.log(this.data);

      if(this.data.length != 0)
      {
        this.userData = data[0];
        this.getItem(this.userData);
      }

      // Function is defined
      function hideloader() {
  
        // Setting display of spinner
        // element to none
        document.getElementById('ContactLoader')
            .style.display = 'none';
      }
    
    });

    this.agentService.getUpdate(this.adapter).subscribe((data:User[]) => 
    {
      this.update = data;
    })
  }

   

  ngOnInit(){
    this.getMessages();
    this.updateUsers();
    this.assigned();
  }

  ngAfterViewChecked() 
  {
    let scrollDiv = document.getElementsByClassName('chat')[0];
    scrollDiv.scrollTop = scrollDiv.scrollHeight;
  }

  public getMessages = () => {
    this.socket.on('message', (message) => {
      console.log(message.user_id, this.userData.id);
        if(message.user_id == this.userData.id)
        {
          console.log(message);
          
          this.logs.push(new Log(message.message,1,'asvdsv', message.isFile));
          console.log(this.logs);
          
        }
    });
}

public updateUsers = () => {
  this.socket.on('update', (user) => 
  {
    console.log('tttt', user.flag, user);
    if(this.agentType == user.flag) {
      console.log('tttt', user.flag);
      
      this.update.push(new User(user.id,user.name, user.flag, false));
     }
  });
}

  getItem(user:User) : void
  {
    this.userData = user;
    this.logService.showItem(this.adapter2,user.id).subscribe((data:Log[]) =>
    {
      this.logs = data;
    })
  }

  assigned()
  {
    this.socket.on('assign',(data) =>
    {
      this.update = this.update.filter((user:User) => 
      {
          console.log(user.id,data.agent,this.agent,data.user.id);
          if(user.id == data.user.id && data.agent == this.agent)
          {
            this.data.push(user);
            this.userData = user;
            this.getItem(this.userData);
            return false;
          }
          else if(user.id == data.user.id && data.agent != this.agent)
          {
            return false;
          }
          else
          {
            return true;
          }
      });
    })
  }

  start(user:User) : void
  {
    document.getElementById('loadChat')
          .style.display = 'block';
    let data = {'user' : user};
    this.agentService.start(data).subscribe((data) =>
    {
      if (data) {
        hideloader();
    }

    });
    // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadChat')
          .style.display = 'none';
    } 
  }

  end(user:User) : void
  {
    let data = {'user' : user};
    this.agentService.end(data).subscribe((data) =>
    {
        if(data == 'success')
        {
          this.data = this.data.filter((dataUser:User) =>
          {
            if(user.id == dataUser.id)
            {
              return false;
            }
            else
            {
              return true;
            }
          })
        }
    },(error) =>{console.log(error);},
    ()=>
    {
      if(this.data.length != 0)
      {
        this.getItem(this.data[0]);
      }
      else
      {
        this.userData = new User(0,'',0,false);
        this.logs = [];
    }
  }
    );
    

  }

  sendMessage(id)
  {
    document.getElementById('loadChat')
          .style.display = 'block';
      this.body = {
        'user_id' : id,
        'answer_id' : this.message,
        'type' : 0
      };

      this.agentService.sendMessage(this.body).subscribe((data) => 
      {
        if (data) {
          hideloader();
      }
          this.logs.push(new Log(this.message,0,data.created_at,data.isFile));
          this.message  = '';
      })
      // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadChat')
          .style.display = 'none';
    }
  }

}
