import {HttpClient , HttpHeaders } from '@angular/common/http';
import { EventEmitter } from 'protractor';
import {BehaviorSubject, Observable} from 'rxjs';
import {map} from 'rxjs/operators';



export class ServiceGeneral<T>
{
    data:T[];
    headers:Array<any>;
    private message:BehaviorSubject<T>;
    public sharedMessage:any;
    protected url:string;
    public total:number;
    public index:number = 1;
    public all:string;
    public httpOptions:object;

    constructor(protected  http:HttpClient) {
        this.httpOptions = {
          headers: new HttpHeaders({
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Content-Type': 'application/json'
          })
        }

     }

    getData(adapter):Observable<T[]> 
    {
      this.httpOptions = {
        headers: new HttpHeaders({
          'Authorization' : `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json'
        })
      };
        return this.http.get<T[]>(`${this.url}?page=${this.index}`,this.httpOptions).pipe(map((data: any)=> {
          this.total = data.last_page;
          return data.data.map((item) =>
          {
            return adapter.adapt(item)
          });
        }
        ));
    }

    showItem(adapter,id):Observable<T[]> 
    {
        return this.http.get<T[]>(`${this.url}/${id}`,this.httpOptions).pipe(map((data: any) => data.data.map((item) => adapter.adapt(item))));
    }

    getItem(adapter,id):Observable<T> 
    {
        return this.http.get<T>(`${this.url}/${id}`,this.httpOptions).pipe(map((data: any) => data.data.map((item) => adapter.adapt(item))));
    }
    
    getHeaders():Array<any> 
    {
      return this.headers;
    }

    deleteData(id):Observable<T[]>
    {
      return this.http.delete<T[]>(`${this.url}/${id}`, this.httpOptions);
    }

    updateData(data:any):Observable<T[]>
    {
      return this.http.post<T[]>(`${this.url}/${data.id}`,data,this.httpOptions);
    }

    showData(data:T)
    {
      this.message = new BehaviorSubject(data);
      this.sharedMessage = this.message.asObservable();
    }

    saveData(data:T)
    {
      return this.http.post<T>(this.url, data,this.httpOptions);
    }

    showAll(adapter)
    {
      return this.http.get<T[]>(`${this.all}`,this.httpOptions).pipe(map((data: any)=> {
        this.total = data.last_page;
        return data.data.map((item) =>
        {
          return adapter.adapt(item)
        });
      }
      ));
    }

}