import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ServiceGeneral } from 'app/interfaces/services';
import { Log } from 'app/models/logs';
import { SharedService } from '../Shared/shared.service';

@Injectable({
  providedIn: 'root'
})
export class LogService extends ServiceGeneral<Log> {

  public api;
  constructor(protected http:HttpClient,private shared:SharedService) {
    super(http);
    this.api = shared.api;
    this.url = `${this.api}/api/logs`;
   }
}
