import { Injectable } from '@angular/core';
import { Adapter } from 'app/adapter';

export class Log{
    public constructor(
        public text:string,
        public type:number,
        public date:string,
        public isFile:boolean,
    ) {}
}

@Injectable({
    providedIn: "root",
  })
  export class LogAdapter implements Adapter<Log> {
    adapt(item: any): Log {
          return new Log(item.text, item.type,item.created_at, item.isFile);
    }
  }